<script>
import { mapState } from 'vuex';
import MaskList from '../../components/mask';
import NavBar from "../../components/NavBar";
import footBar from "../../components/foot";
function IsMobile() {
    var userAgentInfo = navigator.userAgent;
    var Agents = ["Android", "iPhone",
                "SymbianOS", "Windows Phone",
                "iPad", "iPod"];
    var flag = false;
    for (var v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) >= 0) {
            flag = true;
            break;
        }
    }
    return flag;
}
export default {
     components: {
       NavBar,
       MaskList,
       footBar
  },
   computed: {
    ...mapState({
      isMask: state => state.isMask,
      isAndroid: state => state.isAndroid,
      isiOS: state => state.isiOS,
    }),
     i18n(){
          return this.$i18n.locale
      }
  },
  watch: {
    isMask: function(val) {
      const indexH = document.querySelector('.index');
      const mainH = document.documentElement.clientHeight;
      this.$nextTick(() => {
        indexH.style.height =val? mainH +'px' : 100 +'%';
        // indexH.style.overflow = val? 'hidden' : 'visible';
      })
    },
    // 每次切换中英文时
     i18n(){
      this.become = !this.become
        if(IsMobile()){
          this.$nextTick(()=>{
           var innerHight = (this.$refs.contentTgp.offsetHeight) + 60
           this.height = innerHight + ''
          })
      }else{
            this.$nextTick(()=>{
           var innerHight = (this.$refs.contentTgp.offsetHeight) + 200
           this.height = innerHight + ''
          })
      }
       if(this.$i18n.locale=='en'){
           this.become = true
         }else{
           this.become = false   
      }
    }
  },
  data(){
      return{
          app:true,
          news:false,
          play:false,
          become:false,
        //   2900针对pc
          height:"2900",
          add: false
      }
  },
mounted(){
    const u = navigator.userAgent;
    const isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; 
    const isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); 
     if(isAndroid) {
         this.add = true;
     }else {
         this.add = false;
     }
     if(this.$i18n.locale=='en'){
           this.become = true
        //    如果是手机端且是英文时（针对layou组件的高度）
           if(IsMobile()){
                this.height = "2720"
           }  
      }else{
          this.become = false
        //   如果是手机端而且是中文时（针对layou组件的高度）
          if(IsMobile()){
                this.height = "2580"
           }
      }
      if( IsMobile()){
         this.app = true
         this.news = true
         this.play = true
      }

      if(IsMobile()){
        //   针对移动端,,只要组件被渲染,居中内容区与内容区都保持60px
          this.$nextTick(()=>{
           var innerHight = (this.$refs.contentTgp.offsetHeight) + 60
           this.height = innerHight + ''
          })
      }else{
        //   针对pc,只要组件被渲染,pc端无论那个屏幕尺寸,居中内容区与内容区都保持200px
            this.$nextTick(()=>{
           var innerHight = (this.$refs.contentTgp.offsetHeight) + 200
           this.height = innerHight + ''
          })
      }
       window.addEventListener('resize'  , this.handerRize)
},
  methods:{
      go(path){
         this.$router.push(path)
      },
      checkTabs(tabs){
          if(tabs=="app"){
              this.app=true
              this.news=false
              this.play=false
          }
          if(tabs=="news"){
              this.news=true
              this.app=false
              this.play=false
          }
          if(tabs=="play"){
              this.play=true
              this.app=false
              this.news=false
          }
      },
      handerRize(){
        //  针对屏幕大小拖动时 处理内容区和居中主体内容的高度.内容区的高,在屏幕改变到不同的区间,都保持比居中内容高200的值
                        //    平板
                 if(document.body.offsetWidth < 992 && document.body.offsetWidth > 779){
                   
                    this.height = ((this.$refs.contentTgp.offsetHeight) + 200) + ""
                 }      
                    //  大屏
                   if(document.body.offsetWidth > 1200){
                    // this.height = ((contentTgp.offsetHeight) + 200) + ""
                    this.height = ((this.$refs.contentTgp.offsetHeight) + 200) + ""
                 }
                    // 中屏
                  if(document.body.offsetWidth < 1200 && document.body.offsetWidth > 992){
                    this.height = ((this.$refs.contentTgp.offsetHeight) + 200) + ""
                 }       
         }
  },
   destroyed(){
      window.removeEventListener('resize',this.handerRize)
  }
}
</script>
